import { CommonModule } from '@angular/common';
import { Component } from '@angular/core';
import { RouterOutlet } from '@angular/router';
import { NavbarComponent } from './templates/navbar/navbar.component';
import { HeaderComponent, header } from './templates/header/header.component';
import { FooterComponent, map, contact } from './templates/footer/footer.component';
import { SheetsService } from './sheets.service';

@Component({
  selector: 'app-root',
  standalone: true,
  imports: [
    RouterOutlet,
    CommonModule,
    NavbarComponent,
    HeaderComponent,
    FooterComponent,
  ],
  templateUrl: './app.component.html',
  styleUrl: './app.component.less',
  providers: [NavbarComponent, HeaderComponent, FooterComponent],
})
export class AppComponent {
  constructor(
    public pageHeader: HeaderComponent,
    public pageFooter: FooterComponent,
    private sheets: SheetsService
  ) {
    this.registerEvent();
  }

  header: header = this.pageHeader.header;
  contacts: contact[] = this.pageFooter.contacts;
  maps: map[] = this.pageFooter.maps;

  registerEvent() {
    this.sheets.eventAfterRender.subscribe((x) => {
      switch (x.name) {
        case 'app':
          this.header = x.data;
          break;
        case 'contacts':
          this.contacts = x.data;
          break;
        case 'maps':
          this.maps = x.data;
          break;

        default:
          break;
      }
    });
  }
}
