import { Component, OnInit } from '@angular/core';
import { SheetsService, gLinks } from '../../sheets.service';
import { Router } from '@angular/router';
import { CommonModule } from '@angular/common';

export interface header {
  appName: string;
  serviceName: string;
  storeName: string;
  city: string;
  bgUrl: string;
}

@Component({
  selector: 'app-header',
  standalone: true,
  imports: [CommonModule],
  templateUrl: './header.component.html',
  styleUrl: './header.component.less',
})
export class HeaderComponent implements OnInit {
  constructor(private sheets: SheetsService, private router: Router) {}

  url = {
    setup: gLinks.portfolio.tsv.setup,
  };
  header: header = {
    appName: '',
    serviceName: '',
    storeName: '',
    city: '',
    bgUrl: '',
  };

  headerType = {
    height: 'auto',
  };

  ngOnInit() {
    this.router.events.subscribe((x) => {
      // scroll to top on navigated
      if (typeof window != 'undefined') {
        window.scrollTo(0, 0);
      }

      // header height
      this.router.config
        .filter((x) => x.data != undefined)
        .forEach((x) => {
          if ('/' + x.path == this.router.url) {
            this.headerType.height = x.data!['headerHeight'];
          }
        });
    });

    // import data; parse setup parameters from gsheets
    this.sheets.eventLoaded.subscribe((res: { name: string; data: any[] }) => {
      // setup
      if (res.name == 'app') {
        this.header = this.getRenderObject(res.data);
        this.sheets.eventAfterRender.emit({
          name: res.name,
          data: this.header,
        });
      }
    });

    // load gsheets
    this.sheets.load('app', this.url.setup);
  }

  private getRenderObject(data: any[]) {
    let obj: any = {};

    data
      .filter((x) => x.page == 'app')
      .forEach((x) => {
        const name = x.title;
        const val = x.value;
        obj[name] = val;
      });

    return obj;
  }

  protected tracker(i: number, x: any) {
    return x;
  }
}
