import { CommonModule } from '@angular/common';
import { Component, ElementRef, HostListener } from '@angular/core';
import { Router, RouterLink } from '@angular/router';

@Component({
  selector: 'app-navbar',
  standalone: true,
  imports: [CommonModule, RouterLink],
  templateUrl: './navbar.component.html',
  styleUrl: './navbar.component.less',
})
export class NavbarComponent {
  constructor(private router: Router) {}

  open = false;
  links: {
    title: string;
    path: string;
    active: boolean;
  }[] = [];
  protected scrollCss = '';
  protected opacity = 0;

  ngOnInit() {
    this.router.config.filter((x) => x.title != undefined).forEach((x) => {
      this.links.push({
        title: x.title!.toString(),
        path: x.path!,
        active: false,
      });
    });

    this.router.events.subscribe((x: any) => {
      if (x.constructor.name == 'NavigationEnd') this.checkUrl();
    });
  }

  private checkUrl() {
    const url = this.router.url;

    this.links.forEach((f) => {
      console.log(url, f.path);
      f.active = url.indexOf(f.path) > 0;
    });
  }

  // make navbar window gradient opacity on scroll
  @HostListener('window:scroll', ['$event']) // for window scroll events
  protected onScroll(e: any) {
    const elem = document.scrollingElement!;
    const height = document.body.clientHeight;
    const scroll = elem.scrollTop / height;
    this.opacity = scroll;

    // on scroll
    if (scroll > 0.5) {
      this.scrollCss = 'bg-dark';
    } else {
      this.scrollCss = '';
    }
  }

  protected navClicked(e: any) {
    if (e.target.tagName == 'NAV') {
      this.open = false;
    }
  }

  protected tracker(i: number, x: any) {
    return x;
  }
}
