import { CommonModule } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { SheetsService, gLinks } from '../../sheets.service';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

export interface contact {
  type: string;
  platform: string;
  text: string;
  url: string;
  imgUrl: string;
  cssIcon: string;
  cssColor: string;
}
export interface map {
  name: string;
  address: string;
  city: string;
  state: string;
  iframe: SafeResourceUrl;
}

@Component({
  selector: 'app-footer',
  standalone: true,
  imports: [CommonModule],
  templateUrl: './footer.component.html',
  styleUrl: './footer.component.less',
})
export class FooterComponent implements OnInit {
  constructor(private sheets: SheetsService, private sanitize: DomSanitizer) {}

  url = {
    contact: gLinks.portfolio.tsv.contact,
    map: gLinks.portfolio.tsv.map,
  };
  contacts: contact[] = [];
  maps: map[] = [];

  ngOnInit() {
    // import data; parse setup parameters from gsheets
    this.sheets.eventLoaded.subscribe((res: { name: string; data: any[] }) => {
      // contact
      if (res.name == 'contacts') {
        res.data
          .filter((x) => x.platform != undefined)
          .forEach((x) => {
            this.contacts.push(x);
          });
        this.sheets.eventAfterRender.emit({
          name: res.name,
          data: this.contacts,
        });
      }

      // map
      else if (res.name == 'maps') {
        res.data
          .filter((x) => x.address != undefined)
          .forEach((x) => {
            const src = new RegExp(/(?<=src=")\S[^"]*/g).exec(x.iframe);

            const parse: map = {
              name: x.city,
              address: x.address,
              city: x.city,
              state: x.state,
              iframe:
                src != null
                  ? this.sanitize.bypassSecurityTrustResourceUrl(src[0])
                  : '',
            };

            this.maps.push(parse);
          });

        this.sheets.eventAfterRender.emit({ name: res.name, data: this.maps });
      }
    });

    // load gsheets
    this.sheets.load('contacts', this.url.contact);
    this.sheets.load('maps', this.url.map);
  }

  protected tracker(i: number, x: any) {
    return x;
  }
}
